import React from 'react';
import { graphql } from 'gatsby';

import HomeCarousel from '../components/home/homeCarousel';
import VimeoEl from '../components/common/vimeo';
import CreateTiles from '../components/common/createTiles';
import FadeLink from '../components/common/fadeLink';

import homeStyles from '../styles/home.module.scss';

const Home = ({ data }) => {
  const entry = data.sanityHome;

  return (
    <section className="home">
      <HomeCarousel data={[...entry.carousel]} styles={homeStyles} />

      <div className="row">
        <div className="col__2-5 col__m-1-5 col__s-0-5" />
        <div className="col__7 col__m-8-5 col__s-11">
          <div className={homeStyles.heading}>
            <h1 className={`${homeStyles.heading_text} fontSize-h2`}>{entry.intro}</h1>
          </div>
        </div>
        <div className="col__2-5 col__m-1-5 col__s-0-5" />
      </div>

      <div className="row article-media">
        <VimeoEl
          className="vimeo__video aspect__16-by-9"
          vimeoOptions={{ id: entry.vimeo.split('/').pop(), loop: true }}
        />
      </div>

      <div className="row">
        <div className="col__6 col__m-4 col__s-3" />
        <div className="col__6 col__m-8 col__s-9">
          <div className={homeStyles.subheading}>
            <h2 className={`${homeStyles.subheading_text} fontSize-h3`}>{entry.para1}</h2>
          </div>
        </div>
      </div>

      <div className="row">
        <CreateTiles data={entry.featured} />
      </div>

      <div className="row">
        <div className="col__6 col__m-4 col__s-3" />
        <div className="col__6 col__m-8 col__s-9">
          <div className={homeStyles.subheading}>
            <h2 className={`${homeStyles.subheading_text} fontSize-h3`}>{ entry.para2 }</h2>

            <FadeLink className={homeStyles.subheading_link} to="/work">
              <p className={`${homeStyles.subheading_link_text} fontSize-p2`}>
                See more of the work
              </p>
            </FadeLink>
          </div>
        </div>
      </div>
    </section>
  );
};

export default Home;

export const query = graphql`
  {
    sanityHome {
      seo {
        seo_title
        meta_description
      }
      
      carousel {
        project
        description
        image {
          asset {
            id
            url
            title
            fluid(maxWidth: 1920) {
              src
              sizes
              srcSet
              srcWebp
              srcSetWebp
              aspectRatio
            }

            metadata{
              palette{
                dominant{
                  background
                }
              }
            }
          }
        }
        vimeo
        link {
          slug{
            current
          }
        }
      }
      
      intro
      vimeo
      para1
      para2
      
      featured{
        title
        slug{
          current
        }
        tileImage{
          tileDescription
          asset{
            url
            fluid(maxWidth: 650) {
              src
              sizes
              srcSet
              srcWebp
              srcSetWebp
              aspectRatio
            }

            metadata{
              palette{
                dominant{
                  background
                }
              }
            }
          }
        }
      }
    }
  }
`;
